.header-icon {
    height: 50px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-content: center;
    transition: all 0.4;
}

.header-icon img {
    height: 95%;
    width: 95%;
}

header:hover .header-icon {
    animation: rotate 1s 1 ease;
}

@keyframes rotate {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}