.header-item {
    height: 50px;
    width: 50px;
    display: flex;
    margin-left: 10px;
    margin-right: 10px;
}

.header-item:hover {
    border-bottom: 2px inset;
}

.header-item img {
    align-self: center;
    height: 95%;
    width: 95%;
    margin: 2.5%;
}

@media only screen and (min-width: 800px) {
    .header-item img {
        opacity: 0;
        transition: transform 0.4s;
        transform: translateY(-100%);
    }

    header:hover .header-item img {
        transform: translateY(0);
        opacity: 1;
    }
}