.feed-item {
    width: calc(100% - 60px);
    height: auto;
    background-color: rgb(56, 56, 56);
    display: flex;
    justify-content: space-evenly;
    align-content: center;
    padding: 30px;
    z-index: 2;
}

.feed-item img {
    max-height: 80%;
    width: auto;
    align-self: center;
    width: 50%;
    border-radius: 10px;
    box-shadow: 0 0 20px #000;
    transition: all 0.3s ease-out;
}

.feed-item img:hover {
    transform: translate(-5px, -5px);
    box-shadow: 5px 5px 20px #000;
}

.feed-desc {
    color: rgb(209, 209, 209);
    align-self: center;
    text-align: left;
    max-width: 35%;
}

.feed-desc h1 {
    font-size: 40px;
    font-weight: 400;
    /* border-top: solid 3px rgb(209, 209, 209); */
    /* border-spacing: 100%; */
    margin: 0;
}

.feed-desc p {
    font-size: 20px;
}

.feed-btns {
    display: flex;
    justify-content: space-evenly;
}

.feed-btns button {
    height: 45px;
    width: 45%;
    font-size: 20px;
    font-weight: 900;
    color: rgb(150, 150, 150);
    background: linear-gradient(rgba(39, 39, 39, 0.5), rgba(39, 39, 39, 0.6));
    border: unset;
    border-radius: 5px;
    transition: all 0.3s;
}

.feed-btns button:hover, .feed-btns button:focus {
    transform: translate(-2px, -2px);
    box-shadow: 0px 0px 20px rgb(37, 37, 37) inset;
    color: rgb(209, 209, 209);
}

@media only screen and (max-width: 800px) {
    .feed-item {
        flex-direction: column !important;
    }

    .feed-item img {
        width: 100%;
    }

    .feed-desc {
        max-width: 90%;
        margin-bottom: 20px;
    }
}
