@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  /* background-color: rgb(56, 56, 56); */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: inherit;
}
header {
    width: 100%;
    height: 50px;
    background: linear-gradient(rgba(0,0,0,0.8), rgba(0,0,0,0.9));
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    box-shadow: 0 0 30px #000;
    z-index: 2;
}

.header-icon {
    height: 50px;
    width: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    transition: all 0.4;
}

.header-icon img {
    height: 95%;
    width: 95%;
}

header:hover .header-icon {
    -webkit-animation: rotate 1s 1 ease;
            animation: rotate 1s 1 ease;
}

@-webkit-keyframes rotate {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes rotate {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.header-item {
    height: 50px;
    width: 50px;
    display: -webkit-flex;
    display: flex;
    margin-left: 10px;
    margin-right: 10px;
}

.header-item:hover {
    border-bottom: 2px inset;
}

.header-item img {
    -webkit-align-self: center;
            align-self: center;
    height: 95%;
    width: 95%;
    margin: 2.5%;
}

@media only screen and (min-width: 800px) {
    .header-item img {
        opacity: 0;
        transition: -webkit-transform 0.4s;
        transition: transform 0.4s;
        transition: transform 0.4s, -webkit-transform 0.4s;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    header:hover .header-item img {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}
.feed-item {
    width: calc(100% - 60px);
    height: auto;
    background-color: rgb(56, 56, 56);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-content: center;
            align-content: center;
    padding: 30px;
    z-index: 2;
}

.feed-item img {
    max-height: 80%;
    width: auto;
    -webkit-align-self: center;
            align-self: center;
    width: 50%;
    border-radius: 10px;
    box-shadow: 0 0 20px #000;
    transition: all 0.3s ease-out;
}

.feed-item img:hover {
    -webkit-transform: translate(-5px, -5px);
            transform: translate(-5px, -5px);
    box-shadow: 5px 5px 20px #000;
}

.feed-desc {
    color: rgb(209, 209, 209);
    -webkit-align-self: center;
            align-self: center;
    text-align: left;
    max-width: 35%;
}

.feed-desc h1 {
    font-size: 40px;
    font-weight: 400;
    /* border-top: solid 3px rgb(209, 209, 209); */
    /* border-spacing: 100%; */
    margin: 0;
}

.feed-desc p {
    font-size: 20px;
}

.feed-btns {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.feed-btns button {
    height: 45px;
    width: 45%;
    font-size: 20px;
    font-weight: 900;
    color: rgb(150, 150, 150);
    background: linear-gradient(rgba(39, 39, 39, 0.5), rgba(39, 39, 39, 0.6));
    border: unset;
    border-radius: 5px;
    transition: all 0.3s;
}

.feed-btns button:hover, .feed-btns button:focus {
    -webkit-transform: translate(-2px, -2px);
            transform: translate(-2px, -2px);
    box-shadow: 0px 0px 20px rgb(37, 37, 37) inset;
    color: rgb(209, 209, 209);
}

@media only screen and (max-width: 800px) {
    .feed-item {
        -webkit-flex-direction: column !important;
                flex-direction: column !important;
    }

    .feed-item img {
        width: 100%;
    }

    .feed-desc {
        max-width: 90%;
        margin-bottom: 20px;
    }
}

